module.exports = "import { Component, OnInit } from '@angular/core';\n\nexport interface TreeNodeInterface {\n  key: number;\n  name: string;\n  age: number;\n  level: number;\n  expand: boolean;\n  address: string;\n  children?: TreeNodeInterface[];\n}\n\n@Component({\n  selector: 'nz-demo-table-expand-children',\n  template: `\n    <nz-table #nzTable [nzData]=\"data\">\n      <thead>\n        <tr>\n          <th nzWidth=\"40%\">Name</th>\n          <th nzWidth=\"30%\">Age</th>\n          <th>Address</th>\n        </tr>\n      </thead>\n      <tbody>\n        <ng-template ngFor let-data [ngForOf]=\"nzTable.data\">\n          <ng-template ngFor let-item [ngForOf]=\"expandDataCache[data.key]\">\n            <tr *ngIf=\"(item.parent&&item.parent.expand)||!(item.parent)\">\n              <td [nzIndentSize]=\"item.level*20\" [nzShowExpand]=\"!!item.children\" [(nzExpand)]=\"item.expand\" (nzExpandChange)=\"collapse(expandDataCache[data.key],item,$event)\">\n                {{item.name}}\n              </td>\n              <td>{{item.age}}</td>\n              <td>{{item.address}}</td>\n            </tr>\n          </ng-template>\n        </ng-template>\n      </tbody>\n    </nz-table>`,\n  styles  : []\n})\nexport class NzDemoTableExpandChildrenComponent implements OnInit {\n  data = [\n    {\n      key     : 1,\n      name    : 'John Brown sr.',\n      age     : 60,\n      address : 'New York No. 1 Lake Park',\n      children: [\n        {\n          key    : 11,\n          name   : 'John Brown',\n          age    : 42,\n          address: 'New York No. 2 Lake Park'\n        },\n        {\n          key     : 12,\n          name    : 'John Brown jr.',\n          age     : 30,\n          address : 'New York No. 3 Lake Park',\n          children: [ {\n            key    : 121,\n            name   : 'Jimmy Brown',\n            age    : 16,\n            address: 'New York No. 3 Lake Park'\n          } ]\n        },\n        {\n          key     : 13,\n          name    : 'Jim Green sr.',\n          age     : 72,\n          address : 'London No. 1 Lake Park',\n          children: [\n            {\n              key     : 131,\n              name    : 'Jim Green',\n              age     : 42,\n              address : 'London No. 2 Lake Park',\n              children: [\n                {\n                  key    : 1311,\n                  name   : 'Jim Green jr.',\n                  age    : 25,\n                  address: 'London No. 3 Lake Park'\n                },\n                {\n                  key    : 1312,\n                  name   : 'Jimmy Green sr.',\n                  age    : 18,\n                  address: 'London No. 4 Lake Park'\n                }\n              ]\n            }\n          ]\n        }\n      ]\n    },\n    {\n      key    : 2,\n      name   : 'Joe Black',\n      age    : 32,\n      address: 'Sidney No. 1 Lake Park'\n    }\n  ];\n  expandDataCache = {};\n\n  collapse(array: TreeNodeInterface[], data: TreeNodeInterface, $event: boolean): void {\n    if ($event === false) {\n      if (data.children) {\n        data.children.forEach(d => {\n          const target = array.find(a => a.key === d.key);\n          target.expand = false;\n          this.collapse(array, target, false);\n        });\n      } else {\n        return;\n      }\n    }\n  }\n\n  convertTreeToList(root: object): TreeNodeInterface[] {\n    const stack = [];\n    const array = [];\n    const hashMap = {};\n    stack.push({ ...root, level: 0, expand: false });\n\n    while (stack.length !== 0) {\n      const node = stack.pop();\n      this.visitNode(node, hashMap, array);\n      if (node.children) {\n        for (let i = node.children.length - 1; i >= 0; i--) {\n          stack.push({ ...node.children[ i ], level: node.level + 1, expand: false, parent: node });\n        }\n      }\n    }\n\n    return array;\n  }\n\n  visitNode(node: TreeNodeInterface, hashMap: object, array: TreeNodeInterface[]): void {\n    if (!hashMap[ node.key ]) {\n      hashMap[ node.key ] = true;\n      array.push(node);\n    }\n  }\n\n  ngOnInit(): void {\n    this.data.forEach(item => {\n      this.expandDataCache[ item.key ] = this.convertTreeToList(item);\n    });\n  }\n}\n"