module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-table-row-selection',\n  template: `\n    <nz-table\n      #rowSelectionTable\n      [nzData]=\"data\"\n      (nzCurrentPageDataChange)=\"currentPageDataChange($event)\"\n      (nzPageIndexChange)=\"refreshStatus()\"\n      (nzPageSizeChange)=\"refreshStatus()\">\n      <thead>\n        <tr>\n          <th nzShowCheckbox [(nzChecked)]=\"allChecked\" [nzIndeterminate]=\"indeterminate\" (nzCheckedChange)=\"checkAll($event)\"></th>\n          <th>Name</th>\n          <th>Age</th>\n          <th>Address</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr *ngFor=\"let data of rowSelectionTable.data\">\n          <td nzShowCheckbox [(nzChecked)]=\"data.checked\" [nzDisabled]=\"data.disabled\" (nzCheckedChange)=\"refreshStatus()\"></td>\n          <td>{{data.name}}</td>\n          <td>{{data.age}}</td>\n          <td>{{data.address}}</td>\n        </tr>\n      </tbody>\n    </nz-table>`,\n  styles  : []\n})\nexport class NzDemoTableRowSelectionComponent {\n  allChecked = false;\n  indeterminate = false;\n  displayData = [];\n  data = [\n    {\n      name    : 'John Brown',\n      age     : 32,\n      address : 'New York No. 1 Lake Park',\n      checked : false,\n      disabled: false\n    },\n    {\n      name    : 'Jim Green',\n      age     : 42,\n      address : 'London No. 1 Lake Park',\n      checked : false,\n      disabled: false\n    },\n    {\n      name    : 'Joe Black',\n      age     : 32,\n      address : 'Sidney No. 1 Lake Park',\n      checked : false,\n      disabled: false\n    },\n    {\n      name    : 'Disabled User',\n      age     : 32,\n      address : 'Sidney No. 1 Lake Park',\n      checked : false,\n      disabled: true\n    }\n  ];\n\n  currentPageDataChange($event: Array<{ name: string; age: number; address: string; checked: boolean; disabled: boolean; }>): void {\n    this.displayData = $event;\n    this.refreshStatus();\n  }\n\n  refreshStatus(): void {\n    const allChecked = this.displayData.filter(value => !value.disabled).every(value => value.checked === true);\n    const allUnChecked = this.displayData.filter(value => !value.disabled).every(value => !value.checked);\n    this.allChecked = allChecked;\n    this.indeterminate = (!allChecked) && (!allUnChecked);\n  }\n\n  checkAll(value: boolean): void {\n    this.displayData.forEach(data => {\n      if (!data.disabled) {\n        data.checked = value;\n      }\n    });\n    this.refreshStatus();\n  }\n}\n"