module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-table-size',\n  template: `\n    <h4>Middle size table</h4>\n    <nz-table\n      #middleTable\n      nzSize=\"middle\"\n      [nzData]=\"data\">\n      <thead>\n        <tr>\n          <th>Name</th>\n          <th>Age</th>\n          <th>Address</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr *ngFor=\"let data of middleTable.data\">\n          <td>{{data.name}}</td>\n          <td>{{data.age}}</td>\n          <td>{{data.address}}</td>\n        </tr>\n      </tbody>\n    </nz-table>\n    <h4>Small size table</h4>\n    <nz-table\n      #smallTable\n      nzSize=\"small\"\n      [nzData]=\"data\">\n      <thead>\n        <tr>\n          <th>Name</th>\n          <th>Age</th>\n          <th>Address</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr *ngFor=\"let data of smallTable.data\">\n          <td>{{data.name}}</td>\n          <td>{{data.age}}</td>\n          <td>{{data.address}}</td>\n        </tr>\n      </tbody>\n    </nz-table>\n  `,\n  styles  : [\n    `h4 { margin-bottom: 16px; }`\n  ]\n})\nexport class NzDemoTableSizeComponent {\n  data = [\n    {\n      key    : '1',\n      name   : 'John Brown',\n      age    : 32,\n      address: 'New York No. 1 Lake Park',\n    },\n    {\n      key    : '2',\n      name   : 'Jim Green',\n      age    : 42,\n      address: 'London No. 1 Lake Park',\n    },\n    {\n      key    : '3',\n      name   : 'Joe Black',\n      age    : 32,\n      address: 'Sidney No. 1 Lake Park',\n    }\n  ];\n}\n"