module.exports = "import { Component, OnInit } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-table-fixed-columns-header',\n  template: `\n    <nz-table #nzTable [nzData]=\"dataSet\" [nzPageSize]=\"10\" [nzScroll]=\"{ x:'1300px',y: '240px' }\">\n      <thead>\n        <tr>\n          <th nzWidth=\"100px\" nzLeft=\"0px\">Full Name</th>\n          <th nzWidth=\"100px\" nzLeft=\"100px\">Age</th>\n          <th>Column 1</th>\n          <th>Column 2</th>\n          <th>Column 3</th>\n          <th>Column 4</th>\n          <th>Column 5</th>\n          <th>Column 6</th>\n          <th>Column 7</th>\n          <th>Column 8</th>\n          <th nzWidth=\"100px\" nzRight=\"0px\">Action</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr *ngFor=\"let data of nzTable.data\">\n          <td nzLeft=\"0px\">{{data.name}}</td>\n          <td nzLeft=\"100px\">{{data.age}}</td>\n          <td>{{data.address}}</td>\n          <td>{{data.address}}</td>\n          <td>{{data.address}}</td>\n          <td>{{data.address}}</td>\n          <td>{{data.address}}</td>\n          <td>{{data.address}}</td>\n          <td>{{data.address}}</td>\n          <td>{{data.address}}</td>\n          <td nzRight=\"0px\">\n            <a>action</a>\n          </td>\n        </tr>\n      </tbody>\n    </nz-table>`,\n  styles  : []\n})\nexport class NzDemoTableFixedColumnsHeaderComponent implements OnInit {\n  dataSet = [];\n\n  ngOnInit(): void {\n    for (let i = 0; i < 100; i++) {\n      this.dataSet.push({\n        name   : `Edward King ${i}`,\n        age    : 32,\n        address: `London, Park Lane no. ${i}`\n      });\n    }\n  }\n}\n"