module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-table-basic',\n  template: `\n    <nz-table #basicTable [nzData]=\"dataSet\">\n      <thead>\n        <tr>\n          <th>Name</th>\n          <th>Age</th>\n          <th>Address</th>\n          <th>Action</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr *ngFor=\"let data of basicTable.data\">\n          <td>{{data.name}}</td>\n          <td>{{data.age}}</td>\n          <td>{{data.address}}</td>\n          <td>\n            <a>Action  {{data.name}}</a>\n            <nz-divider nzType=\"vertical\"></nz-divider>\n            <a>Delete</a>\n          </td>\n        </tr>\n      </tbody>\n    </nz-table>\n  `\n})\nexport class NzDemoTableBasicComponent {\n  dataSet = [\n    {\n      key    : '1',\n      name   : 'John Brown',\n      age    : 32,\n      address: 'New York No. 1 Lake Park'\n    },\n    {\n      key    : '2',\n      name   : 'Jim Green',\n      age    : 42,\n      address: 'London No. 1 Lake Park'\n    },\n    {\n      key    : '3',\n      name   : 'Joe Black',\n      age    : 32,\n      address: 'Sidney No. 1 Lake Park'\n    }\n  ];\n}\n"