module.exports = "import { Component, OnInit } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-table-row-selection-custom',\n  template: `\n    <nz-table\n      #rowSelectionTable\n      [nzData]=\"dataSet\"\n      [nzPageSize]=\"10\"\n      (nzPageIndexChange)=\"refreshStatus()\"\n      (nzPageSizeChange)=\"refreshStatus()\">\n      <thead>\n        <tr>\n          <th nzShowCheckbox nzShowRowSelection [nzSelections]=\"listOfSelection\" [(nzChecked)]=\"allChecked\" [nzIndeterminate]=\"indeterminate\" (nzCheckedChange)=\"checkAll($event)\"></th>\n          <th>Name</th>\n          <th>Age</th>\n          <th>Address</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr *ngFor=\"let data of rowSelectionTable.data\">\n          <td nzShowCheckbox [(nzChecked)]=\"data.checked\" (nzCheckedChange)=\"refreshStatus()\"></td>\n          <td>{{data.name}}</td>\n          <td>{{data.age}}</td>\n          <td>{{data.address}}</td>\n        </tr>\n      </tbody>\n    </nz-table>`,\n  styles  : []\n})\nexport class NzDemoTableRowSelectionCustomComponent implements OnInit {\n  listOfSelection = [\n    {\n      text    : 'Select All Row',\n      onSelect: () => {\n        this.checkAll(true);\n      }\n    },\n    {\n      text    : 'Select Odd Row',\n      onSelect: () => {\n        this.dataSet.forEach((data, index) => data.checked = index % 2 !== 0);\n        this.refreshStatus();\n      }\n    },\n    {\n      text    : 'Select Even Row',\n      onSelect: () => {\n        this.dataSet.forEach((data, index) => data.checked = index % 2 === 0);\n        this.refreshStatus();\n      }\n    }\n  ];\n  allChecked = false;\n  dataSet: Array<{ name: string; age: number; address: string; checked: boolean }> = [];\n  indeterminate = false;\n\n  refreshStatus(): void {\n    const allChecked = this.dataSet.every(value => value.checked === true);\n    const allUnChecked = this.dataSet.every(value => !value.checked);\n    this.allChecked = allChecked;\n    this.indeterminate = (!allChecked) && (!allUnChecked);\n  }\n\n  checkAll(value: boolean): void {\n    this.dataSet.forEach(data => data.checked = value);\n    this.refreshStatus();\n  }\n\n  ngOnInit(): void {\n    for (let i = 0; i < 46; i++) {\n      this.dataSet.push({\n        name   : `Edward King ${i}`,\n        age    : 32,\n        address: `London, Park Lane no. ${i}`,\n        checked: false\n      });\n    }\n  }\n}\n"